import React from 'react';
import Row from 'antd/es/row';
import Col from 'antd/es/col';
import Button from 'antd/es/button';
import Card from 'antd/es/card';
import ColorSelect from './ColorSelect';
class CustomizeTheme extends React.PureComponent {
    constructor(props) {
        super(props);
        this.setThemeColor = (obj) => {
            const colorMap = Object.assign(Object.assign({}, this.colorMap), obj);
            const { onChange } = this.props;
            if (onChange)
                onChange(colorMap);
        };
        this.submitTheme = () => {
            const { onApply } = this.props;
            if (onApply)
                onApply(this.colorMap);
        };
        this.colorMap = {};
    }
    render() {
        const { value = '{}' } = this.props;
        this.colorMap = value && value !== '' ? JSON.parse(value) || {} : {};
        return (<Card bordered={false} bodyStyle={{ padding: '0px 10px 0px 10px' }}>
        <Row className="chooseColorItem" gutter={24}>
          <Col span={6}>
            <span>头部背景色：</span>
          </Col>
          <Col span={16}>
            <ColorSelect varKey="@head-back_color" value={this.colorMap['@header']} placeholder="请选择头部背景色" onChange={this.setThemeColor}/>
          </Col>
        </Row>
        <Row className="chooseColorItem" gutter={24}>
          <Col span={6}>
            <span>左侧背景色：</span>
          </Col>
          <Col span={16}>
            <ColorSelect value={this.colorMap['@sider']} varKey="@sider-back_color" placeholder="请选择左侧背景色" onChange={this.setThemeColor}/>
          </Col>
        </Row>
        <Row className="chooseColorItem" gutter={24}>
          <Col span={6}>
            <span>左侧字体色：</span>
          </Col>
          <Col span={16}>
            <ColorSelect value={this.colorMap['@font']} varKey="@sider-select_color" placeholder="请选择左侧字体色" onChange={this.setThemeColor}/>
          </Col>
        </Row>
        <Row className="chooseColorItem" gutter={24}>
          <Col span={6}>
            <span>按钮背景色：</span>
          </Col>
          <Col span={16}>
            <ColorSelect value={this.colorMap['@button']} varKey="@select-color" placeholder="请选择按钮背景色" onChange={this.setThemeColor}/>
          </Col>
        </Row>
        <Row className="chooseColorItem" gutter={24}>
          <Col span={6}>
            <span>表头背景色：</span>
          </Col>
          <Col span={16}>
            <ColorSelect value={this.colorMap['@table']} varKey="@table_header_color" placeholder="请选择表头背景色" onChange={this.setThemeColor}/>
          </Col>
        </Row>
        <Row>
          <Col span={24} className="submitArea">
            <Button icon="check" type="primary" onClick={this.submitTheme}>
              预览
            </Button>
          </Col>
        </Row>
      </Card>);
    }
}
export default CustomizeTheme;
